import React, {Component} from 'react';
import "../App.css";
import Axios from 'axios';




class RewardDetails extends Component{
    state={
        thresholdInput: '',
        nameInput: '',
        descriptionInput: '',
        customerResponse: [],
        rewardResponse: {},
        editing: false
    }

    componentWillMount(){
        // const theRestaurantId = this.props.currentUser._id;
        const theRewardId = this.props.match.params.id;
        console.log('component will mount this',this)
        Axios.post('https://lost-in-loyalty.herokuapp.com/api/rewards/approved/'+theRewardId, {}, {withCredentials: true})
        .then((res)=>{
            console.log(res)
            this.setState({customerResponse: res.data.customerResponse, rewardResponse: res.data.rewardResponse})
        })
        .catch((err)=>{
            console.log(err)
        })
    }

    updateInput = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    }

    showUsersForReward = ()=>{

        return this.state.customerResponse.map((user)=>{
            
            return(
                <div className="user-rewards">
                    <h3>Name: {user.name}</h3>
                    <h3>Spent: ${user.spending}</h3>
                </div>
            )
        })
    }

    editReward = (e) =>{
        e.preventDefault();
        const theRewardId = this.props.match.params.id;
        Axios.post('https://lost-in-loyalty.herokuapp.com/api/rewards/edit/'+theRewardId, {theName: this.state.nameInput, theDescription: this.state.descriptionInput, theThreshold: this.state.thresholdInput}, {withCredentials: true})
        .then((res)=>{
            console.log(res)
            this.setState({editing: false})
        })
        .catch((err)=>{
            console.log('error occurred' ,err)
        })
    }

    toggleForm = () =>{
        this.setState({editing: true})
    }

    showRewardsForm = () =>{
        if(this.state.rewardResponse){
            if(this.state.editing){
                return(
                    <form onSubmit={this.editReward}>
                        <input value={this.state.nameInput} onChange={this.updateInput}/>
                        
                        <input value={this.state.descriptionInput} onChange={this.updateInput}/>
                        
                        <input value={this.state.thresholdInput} onChange={this.updateInput}/>

                        <button>Submit</button>
                        
                    
                    </form>
                )
            } else {

                return(

                        <div className="reward-details-b">

                            <h2>{this.state.rewardResponse.name}</h2>

                            <p>{this.state.rewardResponse.description}</p>

                            $ {this.state.rewardResponse.threshold}
                            <button onClick={this.toggleForm}>Edit</button>
                        </div>
                    )

            }
        }
    }


    render(){
        return(
            <div>
            {/* <h2> */}
            <h2>Reward Details: </h2>

            {this.showRewardsForm()}

            <div className="user-rewards-div">
            <h2>Customers for this Reward:</h2>
            {this.showUsersForReward()}
            </div>



            </div>
        )
    }
}


export default RewardDetails;